<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span></span>
      </div>
      <!--  -->
      <el-tree ref="tree" :default-expanded-keys="[1]" lazy node-key="id" :load="loadNode" :props="defaultProps">
        <div class="node-box" slot-scope="{ node, data }">
          <span class="node-name">{{ node.label }}</span>
          <span class="node-action">
						<el-button type="text" icon="el-icon-edit-outline" size="mini" :disabled="data.id==1"
                       @click.stop="openEditModal(node,data)">
							
						</el-button>
						<el-button type="text" icon="el-icon-circle-plus-outline" size="mini" @click.stop="openAddModal(node,data)">
							
						</el-button>
						<el-button type="text" icon="el-icon-delete" size="mini" :disabled="data.id==1"
                       @click.stop="openDeleteModal(node,data)">
							
						</el-button>
					</span>
        </div>
      </el-tree>
    </el-card>
    <!-- Modal -->
    <el-dialog title="" :visible.sync="editModalVisible" :before-close="handleBeforeCloseDialog"
               @closed="handleClosedDialog('editForm')">
      <el-form ref="editForm" :model="editForm" :rules="rules" label-width="80px" :label-position="'left'">
        <el-form-item label="" prop="name">
          <el-input v-model="editForm.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="img">
          <single-upload :data="{type:'common'}" action="/upload/common/" :url.sync="editForm.img"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleCloseDialog"> </el-button>
        <el-button type="primary" @click="handleUpdateNode"> </el-button>
      </div>
    </el-dialog>
    <!-- Modal -->
    <el-dialog title="" :visible.sync="addModalVisible" @closed="handleClosedDialog('addForm')">
      <el-form ref="addForm" :model="addForm" :rules="rules" label-width="80px" :label-position="'left'">
        <el-form-item label="" prop="name">
          <el-input v-model="addForm.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="img">
          <single-upload :data="{type:'common'}" action="/upload/common/" :url.sync="addForm.img"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addModalVisible = false"> </el-button>
        <el-button type="primary" @click="handleAddNode"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { Category } from '@/api/';
import SingleUpload from '@/components/SingleUpload.vue';

export default {
  components: {
    SingleUpload
  },
  data() {
    // 
    var validateImage = (rule, value, callback) => {
      // level === 4
      if (this.currentLevel != 4) {
        return callback();
      }
      // 
      if (value === '') {
        callback(new Error('!'));
      }
      // 
      callback();
    };
    return {
      defaultProps: { label: 'name', },
      headers: { Authorization: `Bearer ${sessionStorage.token}` },
      editModalVisible: false,
      editForm: {
        name: "",
        pId: '',
        img: ''
      },
      addModalVisible: false,
      addForm: {
        name: "",
        pId: '',
        img: ''
      },
      rules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        img: [{ validator: validateImage, trigger: 'blur' }],
      },
      currentNode: '',
      currentLevel: -1,
    }
  },
  created() {
    document.title = "";
  },
  methods: {
    // 
    async loadNode(node, resolve) {
      // level==0
      if (node.level === 0) {
        let { data } = await Category.load({ pId: 0 });
        return resolve(data);
      }
      let { data } = await Category.load({ pId: node.data.id });
      resolve(data);
    },
    // Modal
    openEditModal(node, data) {
      this.editModalVisible = true;
      this.editForm = { ...data };
      // node
      this.currentNode = node;
      //
      this.currentLevel = node.level;
    },
    // 
    handleUpdateNode() {
      // 
      this.$refs.editForm.validate(async (valid) => {
        if (!valid) {
          return false
        }
        let { status, msg } = await Category.update(this.editForm.id, { ...this.editForm });
        if (status) {
          this.$message.success(msg);
          this.editModalVisible = false;
          // DOM
          this.currentNode.data = { ...this.editForm }
        }
      });
    },
    // ModalpId
    openAddModal(node, data) {
      this.addForm.pId = data.id;
      this.addModalVisible = true;
      // data
      this.currentNode = node;
      //
      this.currentLevel = node.level;
    },
    // 
    handleAddNode() {
      // 1.
      this.$refs.addForm.validate(async (valid) => {
        if (!valid) {
          return false
        }
        // 2.
        let { status, msg, data } = await Category.add({ ...this.addForm });
        if (!status) {
          this.$message.error(msg);
          return;
        }
        this.$message.success(msg);
        this.addModalVisible = false;
        // 3.
        this.$refs.tree.append({ ...this.addForm, ...data }, this.currentNode);
      });
    },
    // 
    openDeleteModal(node, data) {
      this.$confirm(', ?', { type: 'warning' })
          .then(async () => {
            let { status, msg } = await Category.remove(data.id);
            if (status) {
              node.remove();
              this.$message.success(msg);
            } else {
              this.$message.error(msg);
            }
          })
          .catch(() => {
            this.$message.info('');
          });
    },
    // 
    checkImage() {
      // currentLevel = 4 
      if (this.currentLevel != 4) {
        return Promise.resolve(true);
      }
      return new Promise((resolve, reject) => {
        this.$refs.editForm.validateField('img', (msg) => {
          if (msg) {
            this.$message.error(msg);
            resolve(false);
          } else {
            resolve(true);
          }
        });
      });
    },
    // dialog
    async handleBeforeCloseDialog(done) {
      let isValid = await this.checkImage();
      if (isValid) {
        done()
      }
    },
    // dialog
    async handleCloseDialog() {
      let isValid = await this.checkImage();
      this.editModalVisible = !isValid;
    },
    // dialog
    handleClosedDialog(formName) {
      this.$refs[formName].resetFields();
    },
  }
}
</script>

<style lang="less" scoped>
.node-box {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: space-between;

  .node-name {
    font-size: 14px;
  }
}
</style>
