import axios from "axios";

// 
const submenu = (params) => axios.get('/menu/sub', { params });

// 
const insert = (data) => axios.post('/menu', data);

// 
const update = (id, data) => axios.put(`/menu/${id}`, data);

// 
const remove = (id, params) => axios.delete(`/menu/${id}`, { params });

// icon
const setIcon = (id, data) => axios.put(`/menu/icon/${id}`, data);

// 
let loadAll = (params) => axios.get('/menu/all', { params });

export default {
    submenu,
    insert,
    update,
    remove,
    setIcon,
    loadAll,
}
