import axios from "axios";

// 
const province = (params) => axios.get('/pcct/province', { params });

// id
const city = (params) => axios.get('/pcct/city', { params });

// id
const county = (params) => axios.get('/pcct/county', { params });

// id()
const town = (params) => axios.get('/pcct/town', { params });

export default {
    province,
    city,
    county,
    town,
}