<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span></span>
      </div>
      <el-form ref="form" :model="form" :rules="rules" label-position="left" label-width="100px">
        <el-form-item label="" prop="username">
          <el-input v-model="form.username" disabled auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="fullname">
          <el-input v-model="form.fullname" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="sex">
          <el-radio-group v-model="form.sex">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="tel">
          <el-input v-model.number="form.tel" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="avatar">
          <single-upload default-image="http://localhost:3003/images/avatar/default.jpg" :data="{type:'avatar'}"
                         action="/upload/common/" :url.sync="form.avatar"/>
        </el-form-item>
        <el-button type="primary" @click="updateInfo"> </el-button>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import SingleUpload from '@/components/SingleUpload.vue';

export default {
  components: {
    SingleUpload,
  },
  data() {
    return {
      roles: [],
      form: {
        username: "",
        fullname: "",
        sex: "",
        tel: "",
        role: "",
        avatar: "",
      },
      rules: {
        fullname: [
          { required: true, type: 'string', message: '', trigger: 'blur' },
          { pattern: /^[\u4E00-\u9FA5A-Za-z\s]+([\u4E00-\u9FA5A-Za-z]+)*$/, message: '', trigger: 'blur' }
        ],
        sex: [
          { required: true, message: '', trigger: 'blur' },
        ],
        tel: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^1(3|4|5|6|7|8|9)\d{9}$/, message: '', trigger: 'blur' }
        ],
        avatar: [
          { required: true, message: '', trigger: 'click' },
        ],
      }
    };
  },
  created() {
    // vuexstate
    this.form = { ...this.$store.state.Account.profile };
    document.title = "";
  },
  beforeRouteLeave(to, from, next) {
    //  `this`
    this.$refs.form.validateField("avatar", (msg) => {
      if (msg) {
        this.$message.error(msg);
        next(false);
      } else {
        next();
      }
    });
  },
  methods: {
    // 
    updateInfo() {
      this.$refs.form.validate((valid) => {
        if (!valid) {
          return false
        }
        ;
        let { role } = this.form;
        this.$store.dispatch('Account/Update', { ...this.form }).then(({ status, msg }) => {
          if (status) {
            sessionStorage.role = role;
            this.$message.success(msg);
          }
        })
      });
    },
  }
}
</script>

<style lang="less" scoped>
.el-form {
  width: 70%;
  margin: 0 auto;
}
</style>
