<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span></span>
      </div>
      <el-alert title="Tips" type="warning" description="" show-icon
                :closable="false">
      </el-alert>
      <!--  -->
      <el-tree :data="treeMenu" ref="tree" :default-expanded-keys="defaultExpandedKeys" node-key="id"
               :props="defaultProps" class="am-margin-top-lg">
        <div class="node-box" slot-scope="{ node, data }">
          <div class="node-name">
            {{ node.label }}
          </div>
          <div class="node-action">
            <el-popover placement="left" class="am-margin-right-sm" trigger="hover">
              <i v-if="data.icon_name" class="am-text-xxl" :class="`el-icon-${data.icon_name}`"></i>
              <div v-else class="am-text-xxl"></div>
              <el-button slot="reference" type="text" :icon="data.icon_name?`el-icon-${data.icon_name}`:''"
                         :disabled="data.id===1"
                         @click.stop="openIconModal(node,data)">
              </el-button>
            </el-popover>
            <el-button type="text" icon="el-icon-edit-outline" :disabled="data.id==1"
                       @click.stop="openEditModal(node,data)">
            </el-button>
            <el-button type="text" icon="el-icon-circle-plus-outline" @click.stop="openInsertModal(node,data)">
              
            </el-button>
            <el-button type="text" icon="el-icon-delete" :disabled="data.id==1"
                       @click.stop="openRemoveModal(node,data)">
            </el-button>
            <div class="node-order am-margin-left-sm">
              
              <el-tag size="mini">{{ data.menu_order }}</el-tag>
            </div>
          </div>
        </div>
      </el-tree>
      <!-- Modal -->
      <el-dialog title="" :visible.sync="edit_modal_visible" @closed="handleClosedDialog('edit_form')">
        <el-form ref="edit_form" :model="edit_form" :rules="rules" label-width="80px" :label-position="'left'">
          <el-form-item label="" prop="name">
            <el-input v-model="edit_form.name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="edit_form.component" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="edit_form.path" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="menu_order">
            <el-input v-model="edit_form.menu_order" placeholder="2001"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="edit_modal_visible = false"> </el-button>
          <el-button type="primary" @click="handleEditMenu"> </el-button>
        </div>
      </el-dialog>
      <!-- Modal -->
      <el-dialog title="" width="72%" :visible.sync="icon_modal_visible">
        <!--  -->
        <ul class="icon-list">
          <li v-for="item in icons" :key="item.id" :class="{checked:item.checked}" @click="handleCheckIcon(item)">
						<span>
							<i :class="`el-icon-${item.name}`"></i>
							<span class="icon-name">el-icon-{{ item.name }}</span>
						</span>
          </li>
        </ul>
        <!--  -->
        <el-pagination background layout="->,prev, pager, next" :page-size="pageSize" :total="total_icon"
                       @current-change="handlePageChange"></el-pagination>
        <div slot="footer" class="dialog-footer">
          <el-button @click="icon_modal_visible = false"> </el-button>
          <el-button type="primary" @click="handleUpdateIcon"> </el-button>
        </div>
      </el-dialog>
      <!-- Modal -->
      <el-dialog title="" :visible.sync="insert_modal_visible" @closed="handleClosedDialog('insert_form')">
        <el-form ref="insert_form" :model="insert_form" :rules="rules" label-width="80px" :label-position="'left'">
          <el-form-item label="" prop="name">
            <el-input v-model="insert_form.name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="insert_form.component" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="insert_form.path" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="menu_order">
            <el-input v-model="insert_form.menu_order" placeholder="2001"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="insert_modal_visible = false"> </el-button>
          <el-button type="primary" @click="handleInsertMenu"> </el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import { Menu, Icon } from "@/api/index";

export default {
  name: "Menu",
  data() {
    return {
      treeMenu: [],
      defaultProps: { label: 'name' },
      // 
      icon_modal_visible: false,
      icons: [],
      pageSize: 30,
      total_icon: 0,
      checked_icon: { icon_id: '', icon: '' },
      // 
      current_node: '',
      //
      defaultExpandedKeys: [1],
      // Modal
      edit_modal_visible: false,
      edit_form: {
        id: '',
        name: '',
        pId: '',
        component: '',
        path: '',
        menu_order: '',
        icon: '',
        icon_id: '',
      },
      // Modal
      insert_modal_visible: false,
      insert_form: {
        name: "",
        pId: '',
        component: "",
        path: '',
        menu_order: '',
        icon: '',
        icon_id: '',
      },
      rules: {
        name: [{ required: true, type: 'string', message: '', trigger: 'blur' }],
        menu_order: [
          { required: true, type: 'string', message: '', trigger: 'blur' },
          { pattern: /^\d+$/, message: '', trigger: 'blur' }
        ],
      }
    }
  },
  created() {
    document.title = "";
    this.loadIcons(1);
    this.loadMenu();
  },
  methods: {
    // 
    async loadMenu() {
      let { status, data } = await Menu.loadAll({ type: 'tree' });
      if (status) {
        this.treeMenu = data;
      }
    },
    // element
    async loadIcons(pageIndex) {
      let { status, data, total } = await Icon.list({ pageSize: this.pageSize, pageIndex });
      if (status) {
        // --
        data.forEach((item) => {
          item.checked = false
        });
        this.icons = data;
        this.total_icon = total;
      }
    },
    // 
    handlePageChange(pageIndex) {
      this.loadIcons(pageIndex);
    },
    // Modal
    openIconModal(node, data) {
      this.icon_modal_visible = true;
      // node
      this.current_node = node;
    },
    // 
    handleCheckIcon(data) {
      this.icons.forEach((item) => {
        // 
        item.checked = item === data ? !data.checked : false;
      });
      // /iconName
      this.checked_icon.icon_id = data.checked ? data.id : '';
      this.checked_icon.icon_name = data.checked ? data.name : '';
    },
    // 
    async handleUpdateIcon() {
      let { id } = this.current_node.data;
      let { status, msg } = await Menu.setIcon(id, { ...this.checked_icon });
      if (status) {
        // 
        this.$message.success(msg);
        // Modal
        this.icon_modal_visible = false;
        // 
        this.current_node.data.icon_name = this.checked_icon.icon_name;
      }
    },
    // 
    openRemoveModal(node, data) {
      this.$confirm(', ?', { type: 'warning', })
          .then(async () => {
            this.setDefaultExpandedKeys()
            let { status, msg } = await Menu.remove(data.id);
            if (status) {
              // remove
              node.remove();
              this.$message.success(msg);
            } else {
              this.$message.error(msg);
            }
          })
          .catch(() => {
            this.$message.info('');
          });
    },
    // ModalpId
    openInsertModal(node, data) {
      this.insert_form.pId = data.id;
      this.insert_modal_visible = true;
      // data
      this.current_node = node;
    },
    // 
    async handleInsertMenu() {
      this.$refs.insert_form.validate(async (valid) => {
        if (valid) {
          //
          this.setDefaultExpandedKeys();
          let { status, msg, data } = await Menu.insert({ ...this.insert_form });
          if (status) {
            // 
            this.$refs.tree.append({ ...this.insert_form, ...data }, this.current_node);
            // 
            this.$message.success(msg);
            // Modal
            this.insert_modal_visible = false;
          } else {
            this.$message.error(msg);
          }
        }
      })
    },
    // Modal
    openEditModal(node, data) {
      this.edit_modal_visible = true;
      //data
      this.edit_form = { ...data };
      // node
      this.current_node = node;
    },
    // 
    async handleEditMenu() {
      let { id } = this.current_node.data;
      this.$refs.edit_form.validate(async (valid) => {
        if (valid) {
          let { status, msg } = await Menu.update(id, { ...this.edit_form });
          if (status) {
            // 
            this.current_node.data = { ...this.edit_form }
            // 
            this.$message.success(msg);
            // Modal
            this.edit_modal_visible = false;
          } else {
            this.$message.error(msg);
          }
        }
      })
    },
    // dialog
    handleClosedDialog(formName) {
      this.$refs[formName].resetFields();
    },
    //
    setDefaultExpandedKeys() {
      // 
      let allNodes = this.$refs.tree.store._getAllNodes();
      let defaultExpandedNodes = [];
      allNodes.forEach(node => {
        node.expanded && defaultExpandedNodes.push(node.data.id);
      });
      this.defaultExpandedKeys = defaultExpandedNodes;
    },
  }
}
</script>

<style lang="less" scoped>
.node-box {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: space-between;
  font-size: 14px;

  .node-order {
    width: 100px;
    display: inline-block;
    font-size: 12px;
  }
}

ul.icon-list {
  overflow: hidden;
  list-style: none;
  padding: 0 !important;
  border: solid 1px #eaeefb;
  border-radius: 4px;
}

.icon-list li {
  float: left;
  width: 10%;
  text-align: center;
  height: 100px;
  line-height: 100px;
  color: #666;
  font-size: 10px;
  border-right: 1px solid #eee;
  border-bottom: 1px solid #eee;
  margin-right: -1px;
  margin-bottom: -1px;

  &::after {
    display: inline-block;
    content: "";
    height: 100%;
    vertical-align: middle;
  }

  span {
    display: inline-block;
    line-height: normal;
    vertical-align: middle;
    font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", SimSun, sans-serif;
    color: #99a9bf;
    transition: color 0.15s linear;
  }

  i {
    display: block;
    font-size: 26px;
    margin-bottom: 15px;
    color: #606266;
    transition: color 0.15s linear;
  }

  .icon-name {
    display: inline-block;
    padding: 0 3px;
    height: 1em;
  }

  &.checked,
  &.checked:hover {
    background-color: rgb(92, 182, 255);

    span,
    i {
      color: white;
    }
  }

  &:hover {

    span,
    i {
      color: rgb(92, 182, 255);
    }
  }
}
</style>
