import axios from 'axios';

// 
const list = (params) => axios.get('/admins/list', { params });

// 
const update = (data) => axios.put('/admins/', data);

// 
const remove = (id, params) => axios.delete(`/admins/${id}`, { params });

// 
const login = (data) => axios.post('/admins/login', data);

// 
const register = (data) => axios.post('/admins/register', data);

// 
const info = (params) => axios.get('/admins', { params });

// 
const account = (data) => axios.put('/admins/account', data);

export default {
    list,
    update,
    remove,
    login,
    register,
    info,
    account,
}
